<template>
<div>
    <div class="cell-info">
        <div class="cell-info-left">
            <div class="img-container">
                <img v-lazy="cellData.headimgurl" class="img" />
            </div>
            <div :class="middleClass">
                <p class="name">{{cellData.username}}</p>
                <p class="icon-phone iconfont"><span class="under-phone">{{cellData.phone}}</span></p>
                <p v-show="cellData.tjtName&&cellData.tjtName!=''" class="bind-info">美导：<span class="bind-name">{{cellData.tjtName}}</span></p>
                <p v-show="cellData.salemanName&&cellData.salemanName!=''" class="bind-info">业务员：<span class="bind-name">{{cellData.salemanName}}</span></p>
            </div>
        </div>
        
        <div class="cell-info-right">
            <mt-button v-if="!cellData.salemanName||cellData.salemanName==''" size="small" class="handle-btn" type="default" @click="bindHandle('3')">绑定业务员</mt-button>
            <mt-button v-else size="small" class="handle-btn dark" type="default" @click="cancelBindHandle('3')">解绑业务员</mt-button>

            <mt-button v-if="!cellData.tjtName||cellData.tjtName==''" size="small" class="handle-btn" type="default" @click="bindHandle('4')">绑定美导</mt-button>
            <mt-button v-else size="small" class="handle-btn dark" type="default" @click="cancelBindHandle('4')">解绑美导</mt-button>
        </div>
    </div>
</div>
</template>

<script>
import Vue from "vue";
import { mapState } from "vuex";
import { Toast, Button } from "mint-ui";
import "mint-ui/lib/button/style.css";
Vue.component(Button.name, Button);
import bus from "../../common/js/eventBus.js";

export default {
  name: "agency-shop-cell",
  data() {
    return {};
  },
  watch: {
    //   ['cellData.belongtoname'](val){
    //       if(cellData.belongtoname&&cellData.belongtoname!=""){
    //           this.middleClass=["cell-info-middle","has-upper"];
    //       }
    //   }
  },
  props: {
    cellData: {
      type: Object,
      default: function() {
        return {
          headimgurl: "http://www.jubenxiong.com/aml/img/nx_1515661463680.png",
          phone: 4001368868,
          username: "L先生",
          salemanName: "",
          salemanId: "",
          tjtName: "",
          tjtId: "",
          userid: "",
          uplist: []
        };
      }
    }
  },
  components: {},
  activated() {},
  mounted() {
    let uplist = this.cellData.uplist;
    for (let i = 0; i < uplist.length; i++) {
      let upItem = uplist[i];
      console.log("upItem.role=" + upItem.role);
      if (upItem.role.toString() == "3") {
        this.$set(this.cellData, "salemanName", upItem.belongtoname);
        this.$set(this.cellData, "salemanId", upItem.belongto);
      } else if (upItem.role.toString() == "4") {
        this.$set(this.cellData, "tjtName", upItem.belongtoname);
        this.$set(this.cellData, "tjtId", upItem.belongto);
      }
    }
  },
  computed: {
    ...mapState(["openid", "path"]),
    middleClass: function() {
      if (this.cellData.salemanName && this.cellData.salemanName != "") {
        return ["cell-info-middle", "has-upper"];
      } else if (this.cellData.tjtName && this.cellData.tjtName != "")
        return ["cell-info-middle", "has-upper"];
      else return ["cell-info-middle"];
    }
  },
  methods: {
    bindHandle(bindRole, event) {
      bus.$emit("shop-bind-call", this.cellData, bindRole);
    },
    cancelBindHandle(cancelRole, event) {
      bus.$emit("shop-bind-cancel-call", this.cellData, cancelRole);
    }
  }
};
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
.cell-info {
    width: 100%;
    height: 82px;
    font-size: 0px;
    background-color: #fff;
    display: flex;
    align-items: center;
    justify-content: space-between;

    .cell-info-left {
        display: flex;
        align-items: center;

        .img-container {
            margin: 0 10px;
            display: inline-block;
            width: 60px;
            height: 60px;

            .img {
                width: 100%;
                height: 100%;
                border-radius: 50%;
                position: relative;
            }
        }

        .cell-info-middle {
            flex: 1;

            .name {
                max-width: 120px;
                overflow: hidden;
                text-overflow: ellipsis;
                white-space: nowrap;
                display: inline-block;
                font-size: 16px;
                font-weight: 700;
                color: #42495C;
                padding-top: 3px;
            }

            .icon-phone {
                position: relative;
                left: -1px;
                font-size: 15px;
                color: #878a94;
                padding-top: 3px;

                .under-phone {
                    color: #aaa;
                    font-size: 13px;
                }
            }

            .bind-info {
                font-size: 13px;
                line-height: 15px;
                color: #aaa;

                .bind-name {
                    font-size: 13px;
                    color: #D2B48C;
                    font-weight: 700;
                }
            }

            &.has-upper {
                .name {
                    font-size: 15px;
                }

                .icon-phone {
                    font-size: 14px;
                    padding: 4px 0px 2px 0px;

                    .under-phone {
                        font-size: 13px;
                    }
                }
            }
        }
    }

    .cell-info-right {
        flex: 0 0 106px;
        text-align: center;

        .handle-btn {
            margin: 5px 0px;
            height: 22px;
            font-size: 12px;
            color: #42495C;
            background-color: #eee;
        }
    }
}
</style>



