<template>
    <div @touchstart="scrollFixStart" @touchmove="scrollFixMove" class="agency-shops-list">
        <ul class="shop-cell-ul" v-infinite-scroll="loadMore" infinite-scroll-disabled="cellLoading" infinite-scroll-distance="0" infinite-scroll-immediate-check="false">
            <li v-for="item in cellData" class="shop-cell-li">
                <agency-shop-cell class="shop-cell" :cell-data="item"></agency-shop-cell>
            </li>
        </ul>
        <div v-if="showEmpty" class="empty-container">
            <empty-bg :msg="emptyMsg"></empty-bg>
        </div>
    </div>
</template>

<script>
import { mapState } from "vuex";
import Vue from "vue";
import { InfiniteScroll } from "mint-ui";
import "mint-ui/lib/toast/style.css";
import agencyShopCell from "../../../public/agencyShopCell.vue";
import EmptyBg from "../../../popupBox/emptyBg.vue";
import bus from "../../../../common/js/eventBus.js";

Vue.use(InfiniteScroll);

export default {
  name: "agency-shops-list",
  data() {
    return {
      emptyMsg: "您还没有商家~",
      showEmpty: false,
      cellLoading: true,
      cellData: [],
      curPage: 0,
      pageNum: 10
    };
  },
  components: {
    EmptyBg,
    agencyShopCell
  },
  activated() {
  },
  created() {
    bus.$on("updata-bind-data", this.updataBindData);
    bus.$on("updata-cancel-bind-data", this.updataCancelBindData);
    this.showEmpty = false;
    this.$http
      .post(this.path + "php/Route.php", {
        type: "allaboss",
        aid: this.userid,
        page: this.curPage
      })
      .then(response => {
        let resultData = response.body.arr;
        if (!resultData || resultData.length == 0) {
          this.showEmpty = true;
        } else {
          this.cellData = resultData;
          this.showEmpty = false;
          if (this.cellData.length == this.pageNum) {
            this.curPage++;
            this.cellLoading = false;
          }
        }
      })
      .catch(response=>{
        //请求出错处理
        console.log(response);
      });
  },

  computed: {
    ...mapState(["openid", "path", "userid"])
  },
  methods: {
    updataBindData(bindShopData, bindUpperData, bindRole) {
      for (var i in this.cellData) {
        let item = this.cellData[i];
        if (item.userid == bindShopData.userid) {
          if (bindRole == "3") {
            this.$set(item, "salemanName", bindUpperData.username);
            this.$set(item, "salemanId", bindUpperData.userid);
          } else {
            this.$set(item, "tjtName", bindUpperData.username);
            this.$set(item, "tjtId", bindUpperData.userid);
          }
        }
      }
    },
    updataCancelBindData(bindShopData,bindRole){
      for (var i in this.cellData) {
        let item = this.cellData[i];
        if (item.userid == bindShopData.userid) {
          if(bindRole=="3"){
            bindShopData.salemanName=null;
            bindShopData.salemanId=null;
          }else{
            bindShopData.tjtName=null;
            bindShopData.tjtId=null;
          }
        }
      }
    },
    loadMore() {
      this.cellLoading = true;
      this.$http
        .post(this.path + "php/Route.php", {
          type: "allaboss",
          aid: this.userid,
          page: this.curPage
        })
        .then(response => {
          let resultData = response.body.arr;
          if (resultData && resultData.length > 0) {
            for (var index in resultData) {
              console.log("index=" + index);
              this.cellData.push(resultData[index]);
            }
            if (resultData.length == this.pageNum) {
              this.curPage++;
              this.cellLoading = false;
            }
          }
        })
        .catch(response=>{
          //请求出错处理
          console.log(response);
        });
    },
    scrollFixStart(e) {
      var el = e.currentTarget;
      if (el.scrollTop === 0) {
        el.scrollTop = 1;
      } else if (el.scrollHeight == el.scrollTop + el.offsetHeight) {
        el.scrollTop = el.scrollTop - 1;
      }
    },
    scrollFixMove(e) {
      var el = e.currentTarget;
      if (el.scrollHeight > el.clientHeight) {
        e.stopPropagation();
      }
    }
  }
};
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
.agency-shops-list {
    width: 100%;
    flex: 1;
    overflow: auto;
    -webkit-overflow-scrolling: touch;
    position: relative;

    .shop-cell-ul {
        width: 100%;

        .shop-cell-li {
            width: 100%;
            // border-bottom 1px solid #eee;
            margin-bottom: 5px;
        }
    }

    .empty-container {
        height: 100%;
        width: 100%;
        position: absolute;
    }
}
</style>



